import {getResource} from '../services/services';

function slider({slide, container, previousArrow, nextArrow, sliderWrapper, field, totalCounter, currentCounter}) {
     //Create slider dynamically

     function createSlider(data) {
          data.forEach(({
               src,
               alt
          }) => {
               const slideWrap = document.createElement('div');
               slideWrap.classList.add(slide.slice(1));
               slideWrap.innerHTML = `
                    <img src=${src} alt=${alt}>
               `;
               document.querySelector(field).append(slideWrap);
          });

     }

     // getResource('http://localhost:3000/slider-images')
     getResource('https://my-json-server.typicode.com/BerLP2021/db/slider-images')
          .then(data => createSlider(data))
          .then(() => {
               // Slider carousel
               const slides = document.querySelectorAll(slide),
                    slider = document.querySelector(container),
                    prev = document.querySelector(previousArrow),
                    next = document.querySelector(nextArrow),
                    wrapper = document.querySelector(sliderWrapper),
                    inner = document.querySelector(field),
                    width = window.getComputedStyle(wrapper).width,
                    total = document.querySelector(totalCounter),
                    current = document.querySelector(currentCounter),
                    dotWrap = document.createElement('ol');

               let slideIndex = 1,
                    offSet = 0,
                    dots = [];

               slider.style.position = 'relative';
               // dotWrap.classList.add('carousel-indicators');
               dotWrap.style.cssText = `
                    position: absolute;
                    right: 0;
                    bottom: 0;
                    left: 0;
                    z-index: 15;
                    display: flex;
                    justify-content: center;
                    margin-right: 15% ;
                    margin-left: 15% ;
                    list-style: none;
               `;
               slider.append(dotWrap);

               for (let i = 0; i < slides.length; i++) {
                    const dot = document.createElement('li');
                    dot.setAttribute('data-slide-to', i + 1);
                    dot.style.cssText = `
                         box-sizing: content-box;
                         flex: 0 1 auto;
                         width: 30px;
                         height: 6px;
                         margin-right: 3px;
                         margin-left: 3px;
                         cursor: pointer;
                         background-color: #fff;
                         background-clip: padding-box;
                         border-top: 10px solid transparent;
                         border-bottom: 10px solid transparent;
                         opacity: .3;
                         transition: opacity .6s ease;
                    `;
                    dotWrap.append(dot);
                    dots[i] = dot;
                    if (i == 0) {
                         dot.style.opacity = 1;
                    }
               }

               // inner.style.cssText = `width = ${(100 * slides.length)}%; display: flex; transition: 0.5s all;`;
               inner.style.width = 100 * slides.length + '%';
               inner.style.display = 'flex';
               inner.style.transition = '0.5s all';
               // slides.forEach(item => {
               //   item.style.width = width;
               // });
               wrapper.style.overflow = 'hidden';
               changePage(slides.length, total);
               changePage(slideIndex, current);

               next.addEventListener('click', () => {
                    if (offSet == pxToNumber(width) * (slides.length - 1)) {
                         offSet = 0;
                         slideIndex = 1;
                    } else {
                         offSet += pxToNumber(width);
                         slideIndex++;
                    }
                    showNextSlide(offSet, slideIndex, current);
               });

               prev.addEventListener('click', () => {
                    if (offSet == 0) {
                         offSet = pxToNumber(width) * (slides.length - 1);
                         slideIndex = slides.length;
                    } else {
                         offSet -= pxToNumber(width);
                         slideIndex--;
                    }
                    showNextSlide(offSet, slideIndex, current);
               });

               function showNextSlide(offSet, slideIndex, current) {
                    inner.style.transform = `translateX(-${offSet}px)`;
                    changePage(slideIndex, current);
                    dots.forEach(dot => dot.style.opacity = '0.5');
                    dots[slideIndex - 1].style.opacity = 1;
               }

               function pxToNumber(str) {
                    return +str.replace(/\D/g, '');
               }

               function changePage(n, elem) {
                    if (n < 10) {
                         elem.textContent = `0${n}`;
                    } else {
                         elem.textContent = n;
                    }
               }

               dots.forEach(dot => {
                    dot.addEventListener('click', (e) => {
                         offSet = (e.target.getAttribute('data-slide-to') - 1) * pxToNumber(width);
                         slideIndex = e.target.getAttribute('data-slide-to');

                         showNextSlide(offSet, slideIndex, current);
                    });
               });
          });

     

     // Slider simple
     // const slides = document.querySelectorAll('.offer__slide'),
     //   prev = document.querySelector('.offer__slider-prev'),
     //   next = document.querySelector('.offer__slider-next'),
     //   total = document.querySelector('#total'),
     //   current = document.querySelector('#current');
     // let slideIndex = 1;

     // showSlide(slideIndex);
     // changePage(slides.length, total);

     // function changePage(n, elem) {
     //   if (n < 10) {
     //     elem.textContent = `0${n}`;
     //   } else {
     //     elem.textContent = n;
     //   }
     // }

     // function showSlide(n) {
     //   if (n > slides.length) {
     //     slideIndex = 1;
     //   } else if (n < 1) {
     //     slideIndex = slides.length;
     //   }
     //   slides.forEach(item => {
     //     item.style.display = 'none';
     //   });
     //   slides[slideIndex - 1].style.display = 'block';
     //   changePage(slideIndex, current);
     // }

     // function slideChange(n) {
     //   showSlide(slideIndex += n);
     // }

     // prev.addEventListener('click', () => {
     //   slideChange(-1);
     // });

     // next.addEventListener('click', () => {
     //   slideChange(1);
     // });
}

export default slider;